import * as PIXI from "pixi.js";

export default class Reel extends PIXI.Container{

    private slotTextures = [
        PIXI.Texture.from('assets/images/Symbol_static/dragon.png'),
        PIXI.Texture.from('assets/images/Symbol_static/fish.png'),
        PIXI.Texture.from('assets/images/Symbol_static/palanquin.png'),
        PIXI.Texture.from('assets/images/Symbol_static/pot.png'),
    ];

    private

    
    private SYMBOL_SIZE = 150;

    public createReel(): any {
        const rc = new PIXI.Container();
        const reel = {
            container: rc,
            symbols: [],
            position: 0,
            previousPosition: 0,
            blur: new PIXI.filters.BlurFilter(),
        };
        reel.blur.blurX = 0;
        reel.blur.blurY = 0;
        rc.filters = [reel.blur];
        for (let j = 0; j < 3; j++) {
            const symbol = new PIXI.Sprite(this.slotTextures[Math.floor(Math.random() * this.slotTextures.length)]);
            // Scale the symbol to fit symbol area.
            symbol.y = j * this.SYMBOL_SIZE;
            symbol.scale.x = symbol.scale.y = Math.min(this.SYMBOL_SIZE / symbol.width, this.SYMBOL_SIZE / symbol.height);
            symbol.x = Math.round((this.SYMBOL_SIZE - symbol.width) / 2);
            reel.symbols.push(symbol);
            rc.addChild(symbol);
        }

    }
}