const configData = require('./../../config.json');
var EventEmitter = require('eventemitter3');
import * as PIXI from 'pixi.js';
var events = require('events');
import ReelContainer from './../Reel/ReelContainer';
var eventEmit = new events.EventEmitter();

export default class BaseView {
    private renderer: PIXI.Renderer;
    private app: PIXI.Application;
    private backGroundImage: PIXI.Sprite;

    private reelContainer: ReelContainer;

    constructor() {
        
    }
    public addBackGround(): void {
        // this.addEventListeners();
        this.app = new PIXI.Application({
            width: configData.width,
            height: configData.height,
            resizeTo: window
        });

        this.backGroundImage = PIXI.Sprite.from('assets/images/basegameBG.jpg');
        this.backGroundImage.width = this.app.screen.width;
        this.backGroundImage.height = this.app.screen.height;

        if (this.backGroundImage.width > this.app.view.width) {
            this.backGroundImage.x = (this.app.stage.x - this.backGroundImage.width) / 2;
        } else {
            this.backGroundImage.x = 0;
        }

        if (this.backGroundImage.height > this.app.view.height) {
            this.backGroundImage.y = (this.app.stage.y - this.backGroundImage.height) / 2;
        } else {
            this.backGroundImage.y = 0;
        }

        let reelCont = new ReelContainer();
        this.app.stage.addChild(this.backGroundImage);
        this.app.stage.addChild(reelCont.createReelView());
        document.body.appendChild(this.app.view);
        // window.addEventListener('resize', this.handleResize, this);
    }

    // private addEventListeners(): void {
    //     eventEmit.addEventListener("window_resized", this.handleResize, this);
    // }

    private handleResize(): void {
        if (window.innerWidth > this.backGroundImage.width) {
            let ratio: number = window.innerWidth / this.backGroundImage.width;
            this.backGroundImage.scale.set(ratio);
            this.backGroundImage.x = 0;
        } else{
            this.backGroundImage.x = (window.innerWidth - this.backGroundImage.width) / 2;
        }

        if (window.innerHeight > this.backGroundImage.height) {
            let ratio: number = window.innerHeight / this.backGroundImage.height;
            this.backGroundImage.scale.set(ratio);
            this.backGroundImage.y = 0;
        } else{
            this.backGroundImage.y = (window.innerHeight - this.backGroundImage.height) / 2;
        }
    }
}