import * as PIXI from "pixi.js";
import Reel from './Reel';

export default class ReelContainer extends PIXI.Container {
    private reelContainer: PIXI.Container;

    private reel: Reel;
    private REEL_WIDTH = 160;
    private reelBG;
    public createReelView(): any {

        this.reelContainer = new PIXI.Container;
        this.addReelBackground();


    }

    private createReel(): void {
        for (let i = 0; i < 5; i++) {
            let reel = new Reel();
            reel.createReel();
            reel.x = i * this.REEL_WIDTH;
            this.reelContainer.addChild(reel as any);
        }

    }

    private addReelBackground(): void {
        this.reelBG = PIXI.Sprite.from('assets/images/reel.png');
        this.reelContainer.addChild(this.reelBG);
        this.createReel();
    }
}